import { Database } from "./database";
import { FileHandle, Normalizer, TheWindow } from "./types";
import Dexie from "dexie";

const win = window as unknown as TheWindow;

const types = [
  {
    description: "Database",
    accept: { "text/plain": [".logs.app"] },
  },
];

// Create an indexeddb store to save handle in a session
const dexie = new Dexie("file-store-handle");
dexie.version(1).stores({ handle: "++id,&name" });
const sessionStore = dexie.table("handle");

export const fileStore = new (class FileStore {
  private databases: { [key: string]: Database } = {};
  private normalizers: { [key: string]: { [key: string]: Normalizer } } = {};

  async pickAndRegisterDatabase(name = "default"): Promise<void> {
    const [file] = await win.showOpenFilePicker({ types });
    if (!file) throw new Error("No file picked");
    return this.registerDatabase(file, name);
  }

  async createAndRegisterDatabase(name = "default"): Promise<void> {
    const file = await win.showSaveFilePicker({ types });
    if (!file) throw new Error("No file picked");
    return this.registerDatabase(file, name);
  }

  async pickFromSessionAndRegisterDatabase(name = "default"): Promise<void> {
    const file = await FileStore.getLastHandle(name);
    if (!file) throw new Error("No file was stored in session");
    return this.registerDatabase(file, name);
  }

  async registerDatabase(handle: FileHandle, name = "default"): Promise<void> {
    const database = new Database(handle, this.normalizers[name] || {});
    if (!(await database.hasPermission())) await database.requestPermission();
    await database.load();
    this.databases[name] = database;
    await FileStore.storeAsLastHandle(name, handle);
  }

  async unregisterDatabase(name = "default"): Promise<void> {
    if (this.databases[name]) delete this.databases[name];
  }

  isDatabaseRegistered(name = "default"): boolean {
    return !!this.databases[name];
  }

  getDatabase(name = "default") {
    const database = this.databases[name];

    if (!database) {
      throw new Error(`Database ${name} is not registered`);
    }

    return database;
  }

  registerSerializer(
    table: string,
    serializer: Normalizer,
    databaseName = "default"
  ): this {
    this.normalizers[databaseName] = this.normalizers[databaseName] || {};
    this.normalizers[databaseName][table] = serializer;
    return this;
  }

  async isDatabaseStored(name = "default"): Promise<boolean> {
    return !!(await FileStore.getLastHandle(name));
  }

  private static async getLastHandle(
    name = "default"
  ): Promise<FileHandle | null> {
    const last = await sessionStore.where("name").equals(name).first();
    return last ? last.handle : null;
  }

  private static async storeAsLastHandle(
    name = "default",
    handle: FileHandle
  ): Promise<void> {
    await sessionStore.where("name").equals(name).delete();
    await sessionStore.add({ name, handle });
  }
})();
