import { Entity, FileHandle, Row, Normalizer } from "./types";

export class Database {
  private readonly mainKey: string;
  private readonly handle: FileHandle;
  public data: { [key: string]: Row[] } = {};
  private dirty = false;
  private normalizers: { [key: string]: Normalizer };

  constructor(
    handle: FileHandle,
    serializers: { [key: string]: Normalizer },
    mainKey = "identifier"
  ) {
    this.handle = handle;
    this.mainKey = mainKey;
    this.normalizers = serializers;
  }

  async load(): Promise<void> {
    const data = await (await this.handle.getFile()).text();
    this.data = data ? JSON.parse(data) : {};
  }

  getHandle(): FileHandle {
    return this.handle;
  }

  isDirty(): boolean {
    return this.dirty;
  }

  create(table: string, row: Row): this {
    if (row[this.mainKey]) {
      throw new Error("Cannot create a row with identifier");
    }

    const normalize =
      this.normalizers[table].normalize ||
      this.getDefaultNormalizer().normalize;

    row.identifier = Date.now();

    this.data[table] = this.data[table] || [];
    this.data[table].push(normalize(row));
    this.dirty = true;
    return this;
  }

  update(table: string, entity: Entity): this {
    const data = this.data[table] || [];

    const normalize =
      this.normalizers[table].normalize ||
      this.getDefaultNormalizer().normalize;

    const row = normalize(entity);

    if (!row.identifier) {
      throw new Error("Cannot update because no identifier present.");
    }

    this.data[table] = data.map((r) =>
      r.identifier === row.identifier ? row : r
    );

    this.dirty = true;
    return this;
  }

  delete(table: string, entity: Entity): this {
    const data = this.data[table] || [];

    const normalize =
      this.normalizers[table].normalize ||
      this.getDefaultNormalizer().normalize;

    const row = normalize(entity);

    this.data[table] = data.filter((r) => r.identifier !== row.identifier);

    this.dirty = true;
    return this;
  }

  all(table: string): Entity[] {
    return (this.data[table] || []).map((row: Row) => {
      const deserialize = this.normalizers[table].denormalize || ((v) => v);
      return deserialize(row);
    });
  }

  async saveAll(): Promise<void> {
    const writeable = await this.handle.createWritable();
    await writeable.write(JSON.stringify(this.data));
    await writeable.close();
    this.dirty = false;
  }

  async requestPermission(): Promise<string> {
    return this.handle.requestPermission({ mode: "readwrite" });
  }

  async hasPermission(): Promise<boolean> {
    return (
      (await this.handle.queryPermission({ mode: "readwrite" })) === "granted"
    );
  }

  getDefaultNormalizer(): Normalizer {
    return {
      normalize: (v) => v,
      denormalize: (v) => v,
    };
  }
}
